#post-title {
    text-align: center;
    margin-top: 20px;
}

#post-div {
    margin: 20px;
    margin-left: 0px;
    font-size: 23px;
    padding-top: 20px;
    max-width: 768px;
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
    display: flex;
    flex-direction: column;
}

.post-content {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.back-blue {
    background-color: #a5e6f3;
}

.post-list {
    padding: 10px 2rem;
    width: 100%;
    justify-content: flex-start;
}

.footer {
    justify-content: space-between;
    display: inline-flex;
}

.edit-comment {
    width: 100%;
    background: transparent;
}

.like-button {
    all: unset;
    text-decoration: none;
    background: none;
    display: block;
}

.like-icon,
.dislike-icon {
    margin-top: 20px;
    display: table;
    font-size: 1.25rem;
}

.like-button:hover:active .like-icon {
    color: red;
}

.like-icon {
    transition: color 0.25s linear;
    color: rgb(206, 203, 203);
}

.dislike-icon {
    color: red;
}

.post-card:hover {
    transform: scale(1.01);
}

.post-card {
    margin: 0px;
    cursor: pointer;
}
